import React from 'react';
import Card from './Card';
import Sdata from './Sdata';

const Services = () => {
    return (
        <>
            <div className="my-5">
                <h1 className="text-center">Welcome to Services page</h1>
            </div>
            <div className="container-fluid mb-5">
                <div className="row">
                    <div className="col-10 mx-auto">
                        <div className="row gy-4">                            
                            {Sdata.map((val,ind)=>{
                                return(<Card imgsrc={val.imgsrc} title={Sdata.title} description={Sdata.details} btnname="View Details"/>)
                            })}
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Services;